import { Grid } from '@material-ui/core';
import React from 'react';
import AccessTimeIcon from '@material-ui/icons/AccessTime';

export default function PlaylistTracks(props) {
	const trackHeadingLengths = [
		{ length: 1, heading: '#' },
		{ length: 4, heading: 'Title' },
		{ length: 4, heading: 'Album' },
		{ length: 2, heading: 'Date Added' },
		{ length: 1, heading: <AccessTimeIcon /> },
	];
	return (
		<Grid item xs={12} container justify="center">
			<Grid
				item
				container
				xs={12}
				justify="center"
				alignItems="flex-end"
				style={{ padding: '20px 20px 10px 20px' }}
			>
				{trackHeadingLengths.map((t, i) => {
					return (
						<Grid
							item
							xs={t.length}
							container
							justify={
								t.heading === trackHeadingLengths[0].heading
									? 'center'
									: t.heading === trackHeadingLengths[4].heading ||
									  t.heading === trackHeadingLengths[3].heading
									? 'flex-end'
									: 'flex-start'
							}
							alignItems="flex-end"
							key={i}
							style={{ color: 'white' }}
						>
							{t.heading}
						</Grid>
					);
				})}
			</Grid>
			{props.currentTracks.map((track, index) => {
				const t = track.track;
				return (
					<Grid
						item
						container
						key={index}
						xs={12}
						className="tracks-heading"
						style={{
							padding: '10px',
							margin: '10px',
						}}
					>
						<Grid
							item
							xs={12}
							justify="center"
							alignItems="flex-start"
							container
							style={{ textOverflow: 'ellipsis', margin: '5px 0' }}
						>
							<Grid
								xs={trackHeadingLengths[0].length}
								item
								container
								justify="center"
								style={{ color: 'white' }}
							>
								{index + 1}
							</Grid>
							<Grid
								xs={trackHeadingLengths[1].length}
								item
								style={{ color: 'white' }}
							>
								{t?.name}
							</Grid>
							<Grid
								xs={trackHeadingLengths[2].length}
								item
								style={{ color: 'white' }}
							>
								{t?.album.name}
							</Grid>
							<Grid
								xs={trackHeadingLengths[3].length}
								item
								container
								justify="flex-end"
								style={{ color: 'white' }}
							>
								{track?.added_at.slice(0, 10)}
							</Grid>
							<Grid
								xs={trackHeadingLengths[4].length}
								item
								container
								justify="flex-end"
								style={{ color: 'white' }}
							>
								{(
									Math.round((t?.duration_ms / 1000) % 60) / 100 +
									Math.floor(t?.duration_ms / 1000 / 60)
								).toFixed(2)}
							</Grid>
						</Grid>
					</Grid>
				);
			})}
		</Grid>
	);
}
